body{
  background-image: url("../../asserts/images/background.png");
  /* background-size: 60%; */
  background-size: cover;
}
.app {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.app-nav{
  display: flex;
  align-items: center;
  padding: 5px 0px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
 .app-nav span{
   color: #fff;
   font-size: 24px;
 }
.app-nav a{
  color: #fff;
  font-size: 24px;
}

.app-boards-container {
  flex: 1;
  width: 100%;
  overflow-x: auto;
  overflow-y: auto;
  height: 100%;
  padding-top: 20px;
}

.app-boards {
  width: fit-content;
  padding: 0px 30px;
  display: inline-flex;
  gap: 30px;
  height: 100%;
}

.app-boards-last {
  flex-basis: 290px;
  min-width: 290px;
}

.app-boards-add-board {
  background-color: #fff;
  color: #000;
  border-radius: 10px;
  box-shadow: 1px 1px 0 1px rgba(0, 0, 0, 0.12);
  width: 100%;
  text-align: center;
}
.app-boards-add-board-edit {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
}
/*
 * scroll bar styling
 */
 *::-webkit-scrollbar {
  width: 8px;
}

/* Track */
*::-webkit-scrollbar-track {
  background: rgba(241, 241, 241, 0);
}

/* Handle */
*::-webkit-scrollbar-thumb {
  background: #c4c4c4;
}

/* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
  background: rgb(217 215 215);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(179, 174, 174, 0.5);
}
